<template>
  <v-container
    fluid
    grid-list-md>
    <v-form>
      <v-layout
        row
        wrap>
        <core-progress-modal :show="loading" />
        <template v-if="!loading">
          <v-flex md12>
            <v-card class="elevation-0">
              <v-card-title>
                <v-layout
                  row
                  wrap
                >
                  <v-flex
                    class="headline mb-0"
                    md12
                  >
                    Boletos de Repasse Gerado
                  </v-flex>
                  <v-flex
                    class="body-1"
                    md12
                  >
                    Pesquise por boletos de repasses já gerados e que ainda não foram pagos,
                    no caso do lojista pedir para fazer o pagamento.
                  </v-flex>
                </v-layout>
              </v-card-title>
              <v-card-text>
                <v-layout
                  row
                  wrap>
                  <v-flex md12>
                    <core-filtro-rede-empresa-loja
                      v-model="form"
                      :show-todas="true"
                      :obrigatorio="true"
                    />
                  </v-flex>
                  <v-flex md2>
                    <v-text-field
                      v-model="form.vencimentoDe"
                      type="date"
                      label="Vencimento De"
                    />
                  </v-flex>
                  <v-flex md2>
                    <v-text-field
                      v-model="form.vencimentoAte"
                      type="date"
                      label="Vencimento Até"
                    />
                  </v-flex>
                  <v-flex md8>
                    <v-checkbox
                      v-model="form.boletoPago"
                      label="Trazer boletos pagos"
                    />
                  </v-flex>
                </v-layout>
                <v-layout class="justify-end">
                  <v-btn
                    :disabled="disableBtn"
                    :loading="loadingBtn"
                    color="primary"
                    @click="consultar">Consultar</v-btn>
                </v-layout>
              </v-card-text>
            </v-card>
            <br>
            <template>
              <v-card>
                <v-data-table
                  :headers="headers"
                  :items="items"
                  class="elevation-1 arrow-pagintion"
                  hide-actions
                >
                  <template
                    slot="items"
                    scope="props"
                    ma-5>
                    <td>
                      {{ props.item.redeNome }}
                    </td>
                    <td>
                      {{ props.item.lojaNome }}
                    </td>
                    <td
                      v-negative="props.item.valorTitulo"
                      class="text-xs-right"
                    >
                      {{ formatValorMonetario(props.item.valorTitulo) }}
                    </td>
                    <td
                      class="text-xs-center"
                    >
                      {{ props.item.dataVencimento | moment("DD/MM/YYYY") }}
                    </td>
                    <td
                      class="text-xs-center"
                    >
                      {{ props.item.dataPagamento | moment("DD/MM/YYYY") }}
                    </td>
                    <td
                      class="text-xs-center"
                    >
                      <v-icon
                        color="primary"
                        title="Download Boleto"
                        @click="baixarBoleto(props.item.anexoId)"
                      >mdi-barcode</v-icon>
                    </td>
                  </template>
                </v-data-table>
              </v-card>
            </template>
          </v-flex>
        </template>
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
import { ReembolsoBusiness, AnexoBusiness } from '../../../business'
import numberUtils from '../../../utils/numberUtils'
import { TODAS_ID } from '../../../constants'
import dateUtils from '../../../utils/dateUtils'
export default {
  data () {
    return {
      form: {
        redeId: TODAS_ID,
        empresaId: TODAS_ID,
        lojaId: TODAS_ID,
        redeNome: null,
        empresaNome: null,
        vencimentoDe: dateUtils.ontem(),
        vencimentoAte: dateUtils.currentDate(),
        lojasSelecionadas: [],
        boletoPago: false
      },
      items: [],
      headers: [
        {
          sortable: false,
          text: 'Rede',
          value: 'redeNome',
          align: 'left',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Empresa (Loja)',
          align: 'left',
          value: 'lojaNome',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Valor Título',
          value: 'valorTitulo',
          align: 'right',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Data Vencimento',
          value: 'dataVencimento',
          align: 'center',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Data Pagamento',
          value: 'dataPagamento',
          align: 'center',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Download',
          align: 'center',
          class: 'grey lighten-1'
        }
      ],
      loading: false,
      loadingBtn: false
    }
  },
  computed: {
    disableBtn () {
      return !(this.form.redeId && this.form.empresaId && this.form.vencimentoAte)
    }
  },
  methods: {
    consultar () {
      this.loadingBtn = this.loading = true
      ReembolsoBusiness.consultarBoletosReembolso(this.form)
        .then(response => {
          this.items = response.data
          if (this.items.length === 0) {
            this.$root.showAlerta('Não foram encontrados registros')
          }
        })
        .catch(() => this.$root.showErro('Erro ao consultar os boletos de Repasse, tentar mais tarde!'))
        .finally(() => {
          this.loadingBtn = this.loading = false
        })
    },
    baixarBoleto (anexoId) {
      AnexoBusiness.downloadAnexo(anexoId)
    },
    formatValorMonetario (valor) {
      return numberUtils.formatValorMonetario(valor)
    }
  }
}
</script>
